:root {
    --main-bg-color: #FFF0DE;
    --main-color: #8D7694;
    --panel-bg-color: #f9f9f9;
    --white-bg-color: #FFFFFF;
    --header-min-height: 64px;
    --footer-min-height: 300px;
    --gap: 12px;
    --radius: 5px;
    --catalog-width: 350px;
    --normal-speed-all: all 0.5s;
    --header-1-font: 14px;
    --multiline: 5;
}

body {
    margin: 0;
    font-family: 'Overpass', sans-serif;
    font-size: 14px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.ellipsis-multiline {
    display: -webkit-box;
    width: 100%;
    overflow: hidden;
    white-space: pre-wrap;
    word-break: break-word;
    text-overflow: ellipsis;
    -webkit-line-clamp: var(--multiline);
    -webkit-box-orient: vertical;
}

.hyphens {
    white-space: pre-wrap;
    word-break: break-word;
    hyphens: auto;
}

.login__panel__social {
    width: 100%;
    height: 64px;
    background: var(--panel-bg-color);
    border-radius: var(--radius);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: var(--gap) 0;
}

.login__panel__add {
    width: 100%;
    background: var(--panel-bg-color);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: var(--gap) 0;
    padding: var(--gap);
    gap: var(--gap);
}

.login__panel__error {
    width: 100%;
    height: 64px;
    background: lightpink;
    border-radius: var(--radius);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: var(--gap) 0;
}

.profile__panel__roles {
    background: var(--panel-bg-color);
    border-radius: var(--radius);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin: var(--gap) 0;
    padding: var(--gap);
    gap: var(--gap);
}

.landing {
    display: grid;
    grid-template-columns: var(--catalog-width) auto;
    margin: var(--gap);
    gap: var(--gap);
    background-color: var(--white-bg-color);
    color: var(--main-color);
    min-height: calc(100vh - var(--header-min-height));
}

.landing__item {
    overflow-x: hidden;
}

.landing__item__line {
    margin: 0 0 8px 0 !important;
    padding: 8px 4px;
}

.landing__item__wrap {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 16px;
    margin: 8px 0 16px 0 !important;
}

.menu {
    max-width: var(--catalog-width);
    width: 100%;
    background-color: var(--white-bg-color);
}

.menu__header {
    display: flex;
    position: relative !important;
}

.menu__item__expand {
    transition: var(--normal-speed-all);
    rotate: 90deg;
}

.menu__item__expand--expand {
    rotate: 180deg;
}

.footer {
    min-height: var(--footer-min-height);
    background-color: var(--main-color);
    color: var(--main-bg-color);
    gap: var(--gap);
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: var(--gap);
}

.footer__logo {
    width: 128px;
    height: 128px;
    border-radius: var(--radius);
}

.header {
    min-height: var(--header-min-height);
    background-color: var(--main-color);
    color: var(--main-bg-color);
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: var(--gap);
    gap: var(--gap);
}

.product__card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 280px;
    width: 280px;
    height: 400px;
    flex-shrink: 0;
    cursor: pointer;
    transition: var(--normal-speed-all);
}


.product__card__image, .product__card__image > svg, .product__card__image > div > img {
    position: absolute;
    top: 0;
    width: 100%;
    height: 165px;
    fill: var(--main-color) !important;
    object-fit: cover;
}

.product__card__header {
    position: absolute;
    top: 178px;
    left: 16px;
    justify-content: flex-end !important;
    width: calc(100% - 32px);
}

.product__card__description {
    position: absolute;
    top: 210px;
    left: 16px;
    justify-content: flex-start !important;
    width: calc(100% - 32px);
}


.product__card__action {
    position: absolute;
    bottom: 0;
    justify-content: flex-end !important;
    gap: 24px;
    width: calc(100% - 20px);
}

.product__card__rating {
    position: absolute !important;
    left: 0;
    bottom: 50px;
    justify-content: center;
    width: 100%;
}

.picture-slider {
    position: relative;
}

.picture-slider__progress {
    position: absolute !important;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    top: 12px;
    left: 12px;
    right: 12px;
    height: 8px;
    transition: var(--normal-speed-all);
    opacity: 1;
}

.picture-slider__progress > svg {
    max-height: 8px;
    height: 8px;
}

.picture-slider__progress--hide {
    opacity: 0;
}

.picture-slider__image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    transition: var(--normal-speed-all);
}
